import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';

import { User } from '@playground/domain';

@Component({
  selector: 'playground-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    class: 'd-block p-3'
  }
})
export class HomeComponent implements OnInit {

  readonly now = new Date();

  readonly user: User = {
    firstName: 'Viatcheslav',
    lastName: 'Zadorozhniy',
    gender: 'male',
    email: 'viatcheslav.zadorozhniy@gmail.com',
  };

  // https://angular.io/api/localize/init/$localize#description
  // https://angular.io/guide/i18n-common-prepare#mark-text-in-component-code
  readonly greetingMessage = $localize`:@@greetingMessage:Glad to see you again, ${this.user.firstName} ${this.user.lastName}`;

  constructor(
    private readonly meta: Meta,
  ) {}

  ngOnInit(): void {
    this.meta.addTag({
      name: 'description',
      content: $localize`:@@homePageMetaDescription:Homepage meta description`,
    });
  }

}
